export default {
	data() {
		return {
			schoolId: 'S000099100001',
			dataRow: [],
			endTime: '',
			index: 0,
			picker: [],
			placeArr: [],
			showBuilding: true,
			origanalArr: [],
			showBack: false,
			finish: 0,
			overDue: 0,
			timeRow: []
		};
	},
	methods: {
		gotoDetail(item) {
			console.log(item)
			if (!item.isSafe) {
				this.showModel('该地点暂未检查！')
			} else {
				uni.navigateTo({
					url: 'FCCdetail/FCCdetail?placeNo='+item.placeNo +"&time=" +this.endTime
				})
			}
		},
		reset() {
			uni.showLoading({
				title:'加载中。。。',
				mask:true
			})
			this.placeArr = this.origanalArr;
			this.showBack = false;
			this.showBuilding = true;
			uni.hideLoading();
		},
		next(foo) {
			this.showBack = true;
			console.log(foo);
			if (foo.ChildYS[0].ChildYS) {
				uni.showLoading({
					title: '加载中。。。',
					mask: true
				});
				this.placeArr = foo.ChildYS;
				uni.hideLoading();
			} else {
				uni.showLoading({
					title: '加载中。。。',
					mask: true
				});
				this.placeArr = foo.ChildYS;
				uni.hideLoading();
				this.showBuilding = false;
			}
			
		},
		PickerChange(e) {
			this.index = e.detail.value;
			console.log(this.picker[this.index])
			var time = this.timeRow[this.index];
			time = time.split('-');
			time = time[1];
			this.endTime = time;
			uni.showLoading({
				title:"加载中。。。",
				mask:true
			});
			this.showBuilding = true;
			this.showBack = false;
			this.getTreeData();
		},

		showModel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
					}
				}
			});
		},
		getXFPlace() {
			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/web/Query_CheckInfo',
					data: {
						campusplaceNo: self.schoolId,
						type: 'XF'
					}
				})
				.then(data => {
					var [error, res] = data;
					console.log(res.data);
					for (let s of res.data.data) {
						let b = s.endTime || '未结束';
						let aa = s.startTime + '-' + b;
						self.timeRow.push(aa)
						self.picker.push(s.XFRoundName);
					}
					var time = this.timeRow[0];
					time = time.split('-');
					time = time[1];
					this.endTime = time;
					this.getTreeData();
				});
		},

		getTreeData() {
			this.finish = this.overDue = 0;
			var data = {};
			if (this.endTime == '未结束') {
				data = {
					campusplaceNo: this.schoolId,
					endTime: ''
				};
			} else {
				data = {
					campusplaceNo: this.schoolId,
					endTime: this.endTime
				};
			}

			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/Web/Get_XF_lastXZ',
					data: data
				})
				.then(data => {
					var [error, res] = data;
					self.placeArr = res.data.data;
					for (let s of self.placeArr) {
						self.overDue = s.overdue + self.overDue;
						self.finish  = s.finish + self.finish;
					}
					self.origanalArr = res.data.data;
					uni.hideLoading();
				});
		}
	},
	onLoad(option) {},
	mounted() {
		uni.showLoading({
			title: '加载中。。。',
			mask: true
		});
		this.getXFPlace();
	}
};
